/* Main column width */
.wp-block {
  max-width: 960px;
  margin-left: auto;
  margin-right: auto;
}

/* Width of "wide" blocks */
.wp-block[data-align=wide] {
  max-width: 1080px;
  margin-left: auto;
  margin-right: auto;
}

/* Width of "full-wide" blocks */
.wp-block[data-align=full] {
  max-width: none;
}

html,
body {
  margin: 0;
  padding: 0;
}

html {
  background-color: var(--wp--preset--color--white-smoke);
  /* ios viewport bug fix */
  height: calc( var(--vh, 1vh) * 100 );
  max-width: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
}

body {
  height: -webkit-fill-available;
  min-height: 100%;
  max-width: 100%;
  box-sizing: border-box;
  overflow-x: hidden;
}

#page {
  overflow-x: hidden;
}

/* Headlines */
body {
  font-family: var(--typography--text--font-family), sans-serif;
  color: var(--color--text);
  font-weight: var(--typography--text--regular);
  font-size: var(--typography--font-size--m);
  line-height: var(--typography--line-height);
  word-break: break-word;
}
@media (max-width : 500px) {
  body {
    font-size: var(--typography--font-size--s);
  }
}

.wp-block.editor-post-title__block .editor-post-title__input, h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--typography--title--font-family);
  font-weight: var(--typography--title--font-weight);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 0 var(--size--margin--);
  padding: 0;
}

h1 {
  font-size: var(--typography--font-size--xxxl);
}
@media (max-width : 500px) {
  h1 {
    font-size: var(--typography--font-size--xxl);
  }
}

h2 {
  font-size: var(--typography--font-size--xxl);
}
@media (max-width : 500px) {
  h2 {
    font-size: var(--typography--font-size--l);
  }
}
.woocommerce h2 {
  font-size: var(--typography--font-size--xl);
}
@media (max-width : 500px) {
  .woocommerce h2 {
    font-size: var(--typography--font-size--l);
  }
}

h3 {
  font-size: var(--typography--font-size--xl);
}
@media (max-width : 767px) {
  h3 {
    font-size: var(--typography--font-size--m);
  }
}

h4 {
  font-size: var(--typography--font-size--l);
}
@media (max-width : 1260px) {
  h4 {
    font-size: var(--typography--font-size--m);
  }
}

h5, h6 {
  font-size: var(--typography--font-size--m);
}
@media (max-width : 1260px) {
  h5, h6 {
    font-size: var(--typography--font-size--s);
  }
}

/* Text and lists */
li, .editor-styles-wrapper li, .editor-styles-wrapper p,
p {
  list-style: none;
  font-size: var(--typography--font-size--m);
}
@media (max-width : 500px) {
  li, .editor-styles-wrapper li, .editor-styles-wrapper p,
p {
    font-size: var(--typography--font-size--s);
  }
}

p, .editor-styles-wrapper li, .editor-styles-wrapper p {
  margin: 0 0 var(--size--margin--);
}

b, strong {
  font-weight: var(--typography--text--bold);
}

a {
  text-decoration: none;
  color: var(--wp--preset--color--primary);
}

ul, ol, dl {
  padding: 0;
}
ul li, ul .editor-styles-wrapper li, .editor-styles-wrapper ul li, ul .editor-styles-wrapper p, .editor-styles-wrapper ul p, ul dt, ul dd, ol li, ol .editor-styles-wrapper li, .editor-styles-wrapper ol li, ol .editor-styles-wrapper p, .editor-styles-wrapper ol p, ol dt, ol dd, dl li, dl .editor-styles-wrapper li, .editor-styles-wrapper dl li, dl .editor-styles-wrapper p, .editor-styles-wrapper dl p, dl dt, dl dd {
  line-height: var(--typography--line-height);
  margin: 0;
}

iframe {
  width: 100%;
  max-width: 100%;
}

::selection {
  background-color: rgba(var(--wp--preset--color--primary--decimal), 0.5);
  color: white;
}

pre {
  width: 100%;
  padding: var(--size--margin-m) var(--size--margin-l);
  background: var(--wp--preset--color--white-smoke);
  border: 1px solid var(--wp--preset--color--gray-light);
  box-sizing: border-box;
  border-radius: 3px;
  max-width: calc(100% - var(--size--responsive--side-margin) * 2);
  overflow-x: auto;
}
pre code {
  border: 0;
  width: 100%;
  display: inherit;
}

code {
  border: 1px solid var(--wp--preset--color--gray-light);
  background-color: var(--wp--preset--color--white-smoke);
  padding: var(--size--margin-xs) var(--size--margin-s);
  margin-top: var(--size--margin-xs);
  margin-bottom: var(--size--margin-xs);
  border-radius: 3px;
  display: inline-block;
  word-break: break-word;
}

cite {
  font-style: italic;
}

.site .entry-content blockquote {
  color: var(--color--text);
  min-height: 60px;
  padding: 30px 90px;
  position: relative;
  border: 1px solid var(--wp--preset--color--gray-light);
}
@media (max-width : 767px) {
  .site .entry-content blockquote {
    padding: var(--size--margin-m) 90px;
  }
}
.site .entry-content blockquote:before {
  font-family: "Material Icons";
  content: "\e244";
  position: absolute;
  top: -4px;
  left: 16px;
  font-size: 60px;
  color: var(--wp--preset--color--secondary);
}
.site .entry-content blockquote p, .site .entry-content blockquote .editor-styles-wrapper li, .editor-styles-wrapper .site .entry-content blockquote li,
.site .entry-content blockquote cite {
  font-size: 1em;
  font-style: italic;
  line-height: var(--typography--line-height--wide);
  font-weight: var(--typography--text--light);
  text-transform: none;
  padding-top: var(--size--margin-m);
  margin-bottom: var(--size--margin-s);
}
.site .entry-content blockquote p em, .site .entry-content blockquote .editor-styles-wrapper li em, .editor-styles-wrapper .site .entry-content blockquote li em,
.site .entry-content blockquote cite em {
  font-style: normal;
}
.site .entry-content blockquote.is-large, .site .entry-content blockquote.is-style-large {
  max-width: 100%;
}
@media (max-width : 767px) {
  .site .entry-content blockquote.wp-block-pullquote {
    padding: var(--size--margin--) var(--size--margin-m);
  }
}

.site .entry-content .wp-block-pullquote {
  border-color: transparent;
  border-width: 2px;
}
.site .entry-content .wp-block-pullquote blockquote {
  margin: 0;
  border: 0;
}
.site .entry-content .wp-block-pullquote blockquote p:first-child, .site .entry-content .wp-block-pullquote blockquote .editor-styles-wrapper li:first-child, .editor-styles-wrapper .site .entry-content .wp-block-pullquote blockquote li:first-child {
  margin-top: 0;
}
.site .entry-content .wp-block-pullquote.alignleft, .site .entry-content .wp-block-pullquote.alignright {
  width: 100%;
}
.site .entry-content .wp-block-pullquote.is-style-solid-color {
  padding-left: 0;
  padding-right: 0;
}
.site .entry-content .wp-block-pullquote.is-style-solid-color blockquote {
  max-width: 100%;
  border: 0;
}
.site .entry-content .wp-block-pullquote.is-style-solid-color blockquote.has-text-color p, .site .entry-content .wp-block-pullquote.is-style-solid-color blockquote.has-text-color .editor-styles-wrapper li, .editor-styles-wrapper .site .entry-content .wp-block-pullquote.is-style-solid-color blockquote.has-text-color li, .site .entry-content .wp-block-pullquote.is-style-solid-color blockquote.has-text-color a, .site .entry-content .wp-block-pullquote.is-style-solid-color blockquote.has-primary-color, .site .entry-content .wp-block-pullquote.is-style-solid-color blockquote.has-secondary-color, .site .entry-content .wp-block-pullquote.is-style-solid-color blockquote.has-dark-gray-color, .site .entry-content .wp-block-pullquote.is-style-solid-color blockquote.has-light-gray-color, .site .entry-content .wp-block-pullquote.is-style-solid-color blockquote.has-white-color {
  color: inherit;
}

@media (max-width : 767px) {
  .site-main .entry-content pre,
.site-main .entry-content blockquote {
    padding-right: var(--size--responsive--side-margin);
    margin-left: var(--size--responsive--side-margin);
    margin-right: var(--size--responsive--side-margin);
    width: auto;
  }
  .site-main .entry-content table {
    width: calc(100% - var(--size--margin-m));
  }
}
@media (max-width : 767px) and (max-width : 767px) {
  .site-main .entry-content table {
    margin-left: 0;
    margin-right: 0;
  }
}
.post-tags {
  list-style: none;
}
.post-tags h3 {
  display: inline;
  vertical-align: middle;
}
.post-tags ul {
  display: inline;
  margin: 0;
  padding-left: var(--size--margin-m);
}
.post-tags li.post-tag, .post-tags .editor-styles-wrapper p.post-tag, .editor-styles-wrapper .post-tags p.post-tag {
  margin: 0 16px 10px 0;
  padding: 0 10px 0 25px;
  line-height: 20px;
}

.post-tag {
  background: var(--wp--preset--color--primary);
  border-radius: 3px 0 0 3px;
  display: inline-block;
  position: relative;
  line-height: 20px;
}
.post-tag a {
  height: 22px;
  line-height: 22px;
  font-size: 11px;
  vertical-align: middle;
  font-weight: var(--typography--text--bold);
  letter-spacing: 1px;
  text-transform: uppercase;
  color: var(--wp--preset--color--white);
  text-decoration: none !important;
}
.post-tag, .post-tag::before, .post-tag::after, .post-tag a {
  transition: background-color 0.2s;
}
.post-tag::before {
  background: #fff;
  border-radius: 10px;
  box-shadow: inset 0 1px rgba(var(--wp--preset--color--white--decimal), 0.25);
  content: "";
  height: 6px;
  left: 10px;
  position: absolute;
  width: 6px;
  top: 8px;
}
.post-tag::after {
  background: transparent;
  border-bottom: 11px solid transparent;
  border-left: 10px solid var(--wp--preset--color--primary);
  border-top: 11px solid transparent;
  content: "";
  position: absolute;
  right: -10px;
  top: 0;
}
.post-tag:hover {
  background-color: var(--wp--preset--color--primary);
}
.post-tag:hover a {
  color: white;
}
.post-tag:hover::after {
  border-left-color: var(--wp--preset--color--primary);
}

hr {
  clear: both;
}